.card {
  flex: 1 0 30%;
  border: 1px $color-1 solid;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  align-items: center;
  max-width: 50%;

  h2 {
    color: $color-2;
  }
  img {
    width: 100%;
    margin: 10px 0;
    border-radius: 15px;
    object-fit: cover;
  }

  h4,
  p {
    color: $color-3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 8;
    overflow: hidden;
  }
}
